// VIP Theme - 纯JavaScript实现

// 主题管理
class ThemeManager {
  constructor() {
    this.theme = localStorage.getItem('theme') || 'light';
    this.themeToggle = document.getElementById('theme-toggle');
    this.sunIcon = document.getElementById('sun-icon');
    this.moonIcon = document.getElementById('moon-icon');
    this.init();
  }

  init() {
    this.applyTheme();
    this.setupEventListeners();
  }

  applyTheme() {
    if (this.theme === 'dark') {
      document.documentElement.classList.add('dark');
      this.sunIcon.classList.remove('hidden');
      this.moonIcon.classList.add('hidden');
    } else {
      document.documentElement.classList.remove('dark');
      this.sunIcon.classList.add('hidden');
      this.moonIcon.classList.remove('hidden');
    }
    
    // 更新footer主题图标
    const footerSunIcon = document.getElementById('footer-sun-icon');
    const footerMoonIcon = document.getElementById('footer-moon-icon');
    if (footerSunIcon && footerMoonIcon) {
      if (this.theme === 'dark') {
        footerSunIcon.classList.remove('hidden');
        footerMoonIcon.classList.add('hidden');
      } else {
        footerSunIcon.classList.add('hidden');
        footerMoonIcon.classList.remove('hidden');
      }
    }
  }

  toggleTheme() {
    this.theme = this.theme === 'light' ? 'dark' : 'light';
    localStorage.setItem('theme', this.theme);
    this.applyTheme();
  }

  setupEventListeners() {
    if (this.themeToggle) {
      this.themeToggle.addEventListener('click', () => this.toggleTheme());
    }
    
    // Footer主题切换按钮
    const footerThemeToggle = document.getElementById('footer-theme-toggle');
    if (footerThemeToggle) {
      footerThemeToggle.addEventListener('click', () => this.toggleTheme());
    }
  }
}

// 移动端菜单管理
class MobileMenu {
  constructor() {
    this.menu = document.getElementById('mobile-menu');
    this.toggle = document.getElementById('mobile-menu-toggle');
    this.menuItems = document.querySelectorAll('.mobile-menu-item');
    this.mobileDropdowns = document.querySelectorAll('.mobile-dropdown-trigger');
    this.init();
  }

  init() {
    this.setupEventListeners();
  }

  setupEventListeners() {
    if (this.toggle) {
      this.toggle.addEventListener('click', () => this.toggleMenu());
    }
    
    // 移动端菜单项点击关闭菜单
    this.menuItems.forEach(item => {
      item.addEventListener('click', () => this.closeMenu());
    });

    // 移动端下拉菜单
    this.mobileDropdowns.forEach(trigger => {
      trigger.addEventListener('click', (e) => {
        e.preventDefault();
        const dropdownId = trigger.getAttribute('data-mobile-dropdown');
        const menu = document.querySelector(`[data-mobile-dropdown-menu="${dropdownId}"]`);
        const arrow = trigger.querySelector('.mobile-dropdown-arrow');
        
        if (menu && arrow) {
          const isOpen = !menu.classList.contains('hidden');
          
          // 关闭所有其他下拉菜单
          document.querySelectorAll('.mobile-dropdown-menu').forEach(openMenu => {
            if (openMenu !== menu) {
              openMenu.classList.add('hidden');
              const openArrow = openMenu.parentElement.querySelector('.mobile-dropdown-arrow');
              if (openArrow) {
                openArrow.style.transform = 'rotate(0deg)';
              }
            }
          });
          
          if (isOpen) {
            menu.classList.add('hidden');
            arrow.style.transform = 'rotate(0deg)';
          } else {
            menu.classList.remove('hidden');
            arrow.style.transform = 'rotate(180deg)';
          }
        }
      });
    });
    
    // 点击菜单外部关闭
    document.addEventListener('click', (e) => {
      if (this.menu && !this.menu.contains(e.target) && this.toggle && !this.toggle.contains(e.target)) {
        this.closeMenu();
      }
    });
  }

  toggleMenu() {
    if (this.menu) {
      const isOpen = this.menu.classList.contains('show');
      if (isOpen) {
        this.closeMenu();
      } else {
        this.open();
      }
    }
  }

  open() {
    if (this.menu) {
      this.menu.classList.add('show');
    }
  }

  closeMenu() {
    if (this.menu) {
      this.menu.classList.remove('show');
    }
  }
}

// 下拉菜单管理
class DropdownMenu {
  constructor() {
    this.dropdowns = document.querySelectorAll('.dropdown-trigger');
    this.init();
  }

  init() {
    this.setupEventListeners();
  }

  setupEventListeners() {
    this.dropdowns.forEach(trigger => {
      trigger.addEventListener('click', (e) => {
        e.preventDefault();
        const dropdownId = trigger.getAttribute('data-dropdown');
        const menu = document.querySelector(`[data-dropdown-menu="${dropdownId}"]`);
        const arrow = trigger.querySelector('.dropdown-arrow');
        
        if (menu && arrow) {
          const isOpen = menu.classList.contains('show');
          
          // 关闭所有其他下拉菜单
          document.querySelectorAll('.dropdown-menu').forEach(openMenu => {
            if (openMenu !== menu) {
              openMenu.classList.remove('show');
              const openArrow = openMenu.parentElement.querySelector('.dropdown-arrow');
              if (openArrow) {
                openArrow.style.transform = 'rotate(0deg)';
              }
            }
          });
          
          if (isOpen) {
            menu.classList.remove('show');
            arrow.style.transform = 'rotate(0deg)';
          } else {
            menu.classList.add('show');
            arrow.style.transform = 'rotate(180deg)';
          }
        }
      });
    });
    
    // 点击外部关闭下拉菜单
    document.addEventListener('click', (e) => {
      if (!e.target.closest('.dropdown-container')) {
        document.querySelectorAll('.dropdown-menu').forEach(menu => {
          menu.classList.remove('show');
          const arrow = menu.parentElement.querySelector('.dropdown-arrow');
          if (arrow) {
            arrow.style.transform = 'rotate(0deg)';
          }
        });
      }
    });
  }
}

// 搜索功能
class SearchManager {
  constructor() {
    this.searchContainer = document.querySelector('.search-container');
    this.searchButton = document.querySelector('.search-button');
    this.searchInput = document.getElementById('search-input');
    this.searchResults = document.getElementById('search-results');
    this.searchIndex = [];
    this.init();
  }

  init() {
    this.loadSearchIndex();
    this.setupEventListeners();
  }

  loadSearchIndex() {
    try {
      // 从页面中加载搜索索引
      const searchIndexElement = document.getElementById('search-index-data');
      if (searchIndexElement) {
        const indexText = searchIndexElement.textContent.trim();
        // 处理双重编码的JSON字符串
        if (indexText.startsWith('"') && indexText.endsWith('"')) {
          const decodedText = JSON.parse(indexText);
          this.searchIndex = JSON.parse(decodedText);
        } else {
          this.searchIndex = JSON.parse(indexText);
        }
        console.log('搜索索引加载成功，共', this.searchIndex.length, '条记录');
      } else {
        console.log('未找到搜索索引数据');
      }
    } catch (error) {
      console.error('加载搜索索引失败:', error);
    }
  }

  setupEventListeners() {
    // 搜索按钮点击事件
    if (this.searchButton) {
      this.searchButton.addEventListener('click', (e) => {
        e.preventDefault();
        this.toggleSearch();
      });
    }

    // 搜索输入框事件
    if (this.searchInput) {
      this.searchInput.addEventListener('input', Utils.debounce((e) => {
        this.handleSearch(e.target.value, this.searchResults);
      }, 300));
      
      this.searchInput.addEventListener('focus', () => {
        if (this.searchResults) {
          this.searchResults.classList.remove('hidden');
        }
      });
      
      // 搜索框失去焦点时隐藏搜索结果
      this.searchInput.addEventListener('blur', () => {
        setTimeout(() => {
          if (this.searchResults && !this.searchResults.contains(document.activeElement)) {
            this.searchResults.classList.add('hidden');
          }
        }, 200);
      });
    }
    
    // 点击外部关闭搜索框和搜索结果
    document.addEventListener('click', (e) => {
      if (this.searchContainer && !this.searchContainer.contains(e.target)) {
        this.closeSearch();
      }
    });
  }

  toggleSearch() {
    if (this.searchContainer) {
      const isOpen = this.searchContainer.classList.contains('show');
      if (isOpen) {
        this.closeSearch();
      } else {
        this.openSearch();
      }
    }
  }

  openSearch() {
    if (this.searchContainer) {
      this.searchContainer.classList.add('show');
      // 聚焦到搜索输入框
      if (this.searchInput) {
        setTimeout(() => {
          this.searchInput.focus();
        }, 100);
      }
    }
  }

  closeSearch() {
    if (this.searchContainer) {
      this.searchContainer.classList.remove('show');
      // 清空搜索输入框
      if (this.searchInput) {
        this.searchInput.value = '';
      }
      // 隐藏搜索结果
      if (this.searchResults) {
        this.searchResults.classList.add('hidden');
      }
    }
  }

  handleSearch(query, resultsContainer) {
    if (query.length < 2) {
      if (resultsContainer) {
        resultsContainer.classList.add('hidden');
      }
      return;
    }
    
    if (resultsContainer) {
      resultsContainer.classList.remove('hidden');
      this.performSearch(query, resultsContainer);
    }
  }

  performSearch(query, resultsContainer) {
    console.log('执行搜索，查询词:', query);
    console.log('搜索索引长度:', this.searchIndex.length);
    
    if (this.searchIndex.length === 0) {
      console.log('搜索索引为空');
      this.displayResults([], resultsContainer, query);
      return;
    }

    const results = this.searchIndex.filter(item => {
      const searchText = [
        item.title || '',
        item.description || '',
        item.summary || '',
        ...(item.tags || []),
        ...(item.categories || [])
      ].join(' ').toLowerCase();
      
      const matches = searchText.includes(query.toLowerCase());
      if (matches) {
        console.log('找到匹配项:', item.title);
      }
      return matches;
    }).slice(0, 10); // 限制结果数量

    console.log('搜索结果数量:', results.length);
    this.displayResults(results, resultsContainer, query);
  }

  displayResults(results, resultsContainer, query) {
    if (!resultsContainer) return;
    
    resultsContainer.innerHTML = '';
    
    if (results.length === 0) {
      resultsContainer.innerHTML = `
        <div class="search-no-results">
          <p>未找到相关结果</p>
        </div>
      `;
      return;
    }
    
    results.forEach(item => {
      const highlightedTitle = this.highlightText(item.title || '', query);
      const highlightedSummary = this.highlightText(item.summary || item.description || '', query);
      const tags = (item.tags || []).slice(0, 3);
      
      const resultItem = document.createElement('div');
      resultItem.className = 'search-result-item';
      resultItem.onclick = () => window.location.href = item.url;
      
      resultItem.innerHTML = `
        <div class="search-result-title">${highlightedTitle}</div>
        <div class="search-result-summary">${highlightedSummary}</div>
        <div class="search-result-meta">
          <div class="search-result-tags">
            ${tags.map(tag => `<span class="search-result-tag">${tag}</span>`).join('')}
          </div>
          <span>${this.formatDate(item.date)}</span>
        </div>
      `;
      
      resultsContainer.appendChild(resultItem);
    });
  }

  highlightText(text, query) {
    if (!query) return text;
    
    const regex = new RegExp(`(${query.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')})`, 'gi');
    return text.replace(regex, '<span class="search-highlight">$1</span>');
  }

  formatDate(dateString) {
    if (!dateString) return '';
    
    const date = new Date(dateString);
    const now = new Date();
    const diffTime = Math.abs(now - date);
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    
    if (diffDays === 1) {
      return '今天';
    } else if (diffDays <= 7) {
      return `${diffDays}天前`;
    } else {
      return date.toLocaleDateString('zh-CN');
    }
  }
}

// 语言切换功能
class LanguageManager {
  constructor() {
    this.languageToggle = document.getElementById('language-toggle');
    this.languageDropdown = document.getElementById('language-dropdown');
    this.languageArrow = document.querySelector('.language-arrow');
    // this.languageOptions = document.querySelectorAll('.language-option');
    this.currentLanguage = document.getElementById('current-language');
    this.init();
  }

  init() {
    this.setupEventListeners();
  }

  setupEventListeners() {
    if (this.languageToggle) {
      this.languageToggle.addEventListener('click', (e) => {
        e.preventDefault();
        this.toggleDropdown();
      });
    }

    // 语言选项点击事件
    // this.languageOptions.forEach(option => {
    //   option.addEventListener('click', (e) => {
    //     e.preventDefault();
    //     const lang = option.getAttribute('data-lang');
    //     const url = option.getAttribute('data-url');
    //     this.switchLanguage(lang, url);
    //   });
    // });

    // 点击外部关闭下拉菜单
    document.addEventListener('click', (e) => {
      if (this.languageToggle && !this.languageToggle.contains(e.target) && 
          this.languageDropdown && !this.languageDropdown.contains(e.target)) {
        this.closeDropdown();
      }
    });
  }

  toggleDropdown() {
    if (this.languageDropdown) {
      const isOpen = this.languageDropdown.classList.contains('show');
      if (isOpen) {
        this.closeDropdown();
      } else {
        this.openDropdown();
      }
    }
  }

  openDropdown() {
    if (this.languageDropdown) {
      this.languageDropdown.classList.add('show');
    }
    if (this.languageArrow) {
      this.languageArrow.classList.add('rotate');
    }
  }

  closeDropdown() {
    if (this.languageDropdown) {
      this.languageDropdown.classList.remove('show');
    }
    if (this.languageArrow) {
      this.languageArrow.classList.remove('rotate');
    }
  }

  switchLanguage(lang, url) {
    // 更新选中状态
    // this.languageOptions.forEach(option => {
    //   if (option.getAttribute('data-lang') === lang) {
    //     option.classList.add('selected');
    //   } else {
    //     option.classList.remove('selected');
    //   }
    // });

    // 关闭下拉菜单
    this.closeDropdown();

    // 跳转到对应语言页面
    if (url) {
      // 确保URL是完整的路径
      const currentPath = window.location.pathname;
      let targetUrl = url;
      
      // 如果是当前页面，直接跳转到对应语言版本
      if (url === window.location.pathname || url === window.location.pathname + '/') {
        if (lang === 'zh-ch') {
          // 切换到中文，移除/en前缀
          targetUrl = currentPath.replace(/^\/en/, '');
        } else {
          // 切换到英文，添加/en前缀
          targetUrl = currentPath.startsWith('/en') ? currentPath : `/en${currentPath}`;
        }
      }
      
      console.log('Switching to language:', lang, 'URL:', targetUrl);
      window.location.href = targetUrl;
    }
  }
}

// 滚动效果
class ScrollEffects {
  constructor() {
    this.header = document.getElementById('main-header');
    this.scrollTopBtn = document.getElementById('scroll-top');
    this.init();
  }

  init() {
    this.setupScrollEffects();
  }

  setupScrollEffects() {
    // 导航栏滚动效果
    if (this.header) {
      window.addEventListener('scroll', () => {
        if (window.scrollY > 100) {
          this.header.classList.add('scrolled');
        } else {
          this.header.classList.remove('scrolled');
        }
      });
    }
    
    // 滚动到顶部按钮
    if (this.scrollTopBtn) {
      window.addEventListener('scroll', () => {
        if (window.scrollY > 300) {
          this.scrollTopBtn.classList.add('show');
        } else {
          this.scrollTopBtn.classList.remove('show');
        }
      });
      
      this.scrollTopBtn.addEventListener('click', () => {
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      });
    }
  }
}

// 工具函数
const Utils = {
  debounce(func, wait) {
    let timeout;
    return function executedFunction(...args) {
      const later = () => {
        clearTimeout(timeout);
        func(...args);
      };
      clearTimeout(timeout);
      timeout = setTimeout(later, wait);
    };
  },

  throttle(func, limit) {
    let inThrottle;
    return function() {
      const args = arguments;
      const context = this;
      if (!inThrottle) {
        func.apply(context, args);
        inThrottle = true;
        setTimeout(() => inThrottle = false, limit);
      }
    };
  },

  formatDate(date, locale = 'zh-CN') {
    return new Intl.DateTimeFormat(locale, {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }).format(new Date(date));
  },

  copyToClipboard(text) {
    if (navigator.clipboard) {
      return navigator.clipboard.writeText(text);
    } else {
      const textArea = document.createElement('textarea');
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      return Promise.resolve();
    }
  }
};

